import { Component, OnInit } from '@angular/core';
import { CountingService } from '../services/http/counting.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {

  URL = '';
  subject = '';
  answer = '';
  id = '';

  constructor(private countingService: CountingService) { }

  ngOnInit() {
    this.countingService.getCountingData().subscribe(
      (param: {imageUrl: string, answer: string, subject: string ,id: string}[]) => {
        this.id = String(+param[param.length-1].id + 1);
      }
    )
  }

  onPost() {
    let counitngData: {imageUrl: string, answer: string, subject: string, id: string} =
    {imageUrl: this.URL, answer: this.answer, subject: this.subject, id: this.id};

    this.countingService.postCountingData(counitngData).subscribe(
      (param) => {

      }
    )
  }
}
